.clearfix {
  zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
  visibility: hidden;
  font-size: 0;
  height: 0;
}
.yt-header-box {
  zoom: 1;
  padding: 0;
  -webkit-box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.15);
  z-index: 10;
  background: #ffffff;
  height: 56px;
  position: relative;
}
.yt-header-box:before,
.yt-header-box:after {
  content: " ";
  display: table;
}
.yt-header-box:after {
  clear: both;
  visibility: hidden;
  font-size: 0;
  height: 0;
}
.yt-header-box .yt-header-logo {
  float: left;
  width: 110px;
  text-align: center;
  background-image: -webkit-gradient(linear, right top, left top, from(#61A6F6), to(#4693F0));
  background-image: -o-linear-gradient(right, #61A6F6, #4693F0);
  background-image: linear-gradient(to left, #61A6F6, #4693F0);
}
.yt-header-box .yt-header-title {
  float: left;
  padding: 0 16px;
  font-size: 20px;
  color: #4997f1;
  vertical-align: middle;
}
.yt-header-box .yt-header-extra {
  float: left;
}
.yt-header-box .yt-header-right {
  float: right;
  padding-right: 16px;
}
.yt-header-box .yt-header-right .yt-header-menu {
  list-style: none;
  display: inline-block;
  margin: 0 24px 0 0;
  padding: 0;
}
.yt-header-box .yt-header-right .yt-header-menu li {
  display: inline-block;
}
.yt-header-box .yt-header-right .yt-header-menu li .yt-header-menu-item-divider {
  margin: 8px;
  color: #4997f1;
}
.yt-header-box .yt-header-right .yt-header-user {
  margin-right: 16px;
}
