@font-face {
  font-family: 'admin-menu-iconfont';
  /* project id 662941 */
  src: url('//at.alicdn.com/t/font_662958_b24mgwrb1fc4n29.eot');
  src: url('//at.alicdn.com/t/font_662958_b24mgwrb1fc4n29.eot?#iefix') format('embedded-opentype'), url('//at.alicdn.com/t/font_662958_b24mgwrb1fc4n29.woff') format('woff'), url('//at.alicdn.com/t/font_662958_b24mgwrb1fc4n29.ttf') format('truetype'), url('//at.alicdn.com/t/font_662958_b24mgwrb1fc4n29.svg#font_family') format('svg');
}
.admin-menu-iconfont {
  font-family: "admin-menu-iconfont" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.admin-menu-icon {
  font-size: 14px;
  margin-right: 10px;
}
.admin-menu-icon-img {
  height: 14px;
  width: 14px;
  margin-right: 10px;
  opacity: 0.65;
}
.admin-menu-aside.ant-layout-sider {
  background: #2E323E;
}
.menu-container {
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.menu-container .system-logo {
  height: 56px;
  line-height: 56px;
  width: 100%;
  background: #4997F1;
  text-align: center;
}
.menu-container .system-logo img {
  width: 102px;
  height: 32px;
}
.menu-container .menu-switch-container {
  padding: 16px 8px 8px;
  display: inline-flex;
  justify-content: space-between;
}
.menu-container .menu-switch-container .menu-switch-text {
  color: #fff;
  margin-right: 16px;
}
.menu-container .menu-search {
  padding: 8px;
}
.menu-container .main-menu {
  flex: 1 1;
  overflow: auto;
}
.menu-container .ant-menu-dark {
  background: #2E323E;
}
.menu-container .ant-menu .ant-menu-item {
  margin-top: 0;
  margin-bottom: 0;
}
.menu-container .ant-menu-dark .ant-menu-item:hover .admin-menu-icon-img,
.menu-container .ant-menu-dark .ant-menu-item-active .admin-menu-icon-img,
.menu-container .ant-menu-dark .ant-menu-submenu-active .admin-menu-icon-img,
.menu-container .ant-menu-dark .ant-menu-submenu-open .admin-menu-icon-img,
.menu-container .ant-menu-dark .ant-menu-submenu-selected .admin-menu-icon-img,
.menu-container .ant-menu-dark .ant-menu-submenu-title:hover .admin-menu-icon-img {
  opacity: 1;
}
